@import '../../base';

.motor-more {
  .landing-form-outer-width {
    margin: auto;
    max-width: 945px;
    flex: 0 0 100%;
    margin-bottom: 50px;

    @media screen and (min-width: 576px) {
      padding-left: 20px;
    }
  }

  .input-offset {
    @media screen and (max-width: 576px) {
      margin-top: 0;
    }
  }

  .demerit-selection {
    @media screen and (min-width: 576px) {
      .text-sel-col:nth-of-type(1) {
        padding: 0;
        padding-right: 10px;
      }
  
      .text-sel-col:nth-of-type(2) {
        padding: 0;
        padding-left: 10px;
      }
    }
  
    @media screen and (min-width: 1040px) {
      .text-sel-col:nth-of-type(1) {
        padding: 0;
        padding-right: 20px;
      }
  
      .text-sel-col:nth-of-type(2) {
        padding: 0;
        padding-left: 20px;
      }
    }
  }

  .error-string {
    margin-top: 20px;
    font-size: $font-size-regular;
    font-weight: $font-weight-semibold;
    color: $color-error-alt;

    .error-sub {
      display: block;
    }
  }
}
